@import url("https://fonts.googleapis.com/css2?family=Dosis:wght@200..800&family=Montserrat:ital,wght@0,100..900;1,100..900&family=Rubik:ital,wght@0,300..900;1,300..900&display=swap");
* {
  font-family: "Rubik", sans-serif;
  font-weight: 350 !important;
  letter-spacing: 2px;
  word-spacing: 4px;
  scroll-behavior: smooth;
}
html {
  scroll-behavior: smooth;
  overflow-x: hidden;
}
:root {
  --primary-color: #5c2e97;
  --secondary-color: #67b637;
  --black-color: #000;
}
.main-page-header {
  height: 80px;
  /* width: 100%; */
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 4rem;
  background-color: #fff;
  color: var(--black-color);
  /* box-shadow: 0 2px 5px rgba(0,0,0,0.1); */
  z-index: 100;
  position: relative;
}
.main-page-header img {
  height: 50px;
}
.main-page-header nav {
  display: flex;
  align-items: center;
  height: 60px;
  width: fit-content;
}
.main-page-header nav ul {
  display: flex;
  list-style: none;
}
.main-page-header nav ul li:first-child {
  margin-right: 3rem;
}
.main-page-header nav ul li a {
  text-decoration: none;
  color: #fff;
  background: linear-gradient(45deg, #3281e2, #a31cea, var(--primary-color));
  padding: 0.8rem 1.2rem;
  border-radius: 24px;
  transition: color 0.15s ease-in-out;
}
.main-page-header nav ul li a:hover {
  color: #ffffff00;
}
