    content: '';
    display: block;
    width: 0;
    height: 2px;
    background-color: #fff;
    transition: width 0.15s;
}
.main-page-header nav ul li a:hover::after{
    width: 100%;
} */
.main {
  display: flex;
  justify-content: center;
  align-items: center;
  height: calc(100vh - 160px);
  background: url("./Images/963006959fadd3a9fdcfa557e5469cf9.jpg") fixed
    no-repeat center top/cover;
  background-color: #000;
  position: relative;
}
.main .glass {
  background-color: rgba(255, 255, 255, 0.1); /* Adjusted for glassmorphism */
  border-radius: 12px;
  padding: 3rem 4rem;
  display: flex;
  flex-direction: column;
  transition: background-color 0.2s ease-in-out;
  /* width: 60%; */
  margin: 2rem auto;
  box-shadow: rgba(94, 94, 94, 0.322) 0px 1px 4px;
  position: relative;
  z-index: 2;
  backdrop-filter: blur(5px); /* Ensure this is present for the blur effect */
  border: 1px solid rgba(255, 255, 255, 0.18);
  background: linear-gradient(270deg, #ffffffbb, #ffffffd5) fixed;
  box-shadow: 0 8px 32px 0 rgba(31, 38, 135, 0.37);
  max-width: 850px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: end;
}
.glass-text {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: end;
}
.main .blobs {
  position: absolute;
  top: 0;
  left: 0;
  width: 80%;
  margin: 2rem 5%;
  height: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  z-index: 0; /* Ensure it's below .glass */
}

.main .blob img {
  height: 100px;
  width: 100px;
  object-fit: contain;
  position: relative;
}
.glass h1 {
  font-size: 3rem;
  font-weight: 400 !important;
  width: 100%;
  letter-spacing: 4px;
  word-spacing: 5px;
  line-height: 4rem;
  color: var(--black-color);
  background: linear-gradient(0deg, #3281e2, #4f0e72, var(--primary-color))
    fixed;
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  margin-bottom: 1rem;
}
.glass h2 {
  font-size: 2rem;
  /* font-weight: 400; */
  color: var(--black-color);
}
.glass h2 span {
  color: var(--primary-color);
  /* font-weight: 700; */
}
.glass a,
.benefits-designers a {
  text-decoration: none;
  color: #fff;
  background: linear-gradient(45deg, #3281e2, #a31cea, var(--primary-color));
  padding: 0.8rem 1.2rem;
  border-radius: 24px;
  /* font-weight: 600; */
  transition: background-color 0.15s ease-in-out;
  margin-top: 3rem;
  right: 0;
  display: flex;
  justify-content: end;
  width: fit-content;
  justify-self: flex-end;
  align-items: center;
}
.glass a:hover {
  background-color: #4d8c2b;
}
.glass a::after,
.benefits-designers a::after {
  content: "â†’";
  display: block;
  width: 16px;
  font-size: 1rem;
  font-family: "roboto";
  margin-left: 5px;
  transition: all 0.15s;
}
.glass a:hover::after,
.benefits-designers a:hover::after {
  margin-left: 20px;
}
.scroll-down {
  position: absolute;
  bottom: 2rem;
  left: 10%;
  transform: translateX(-50%);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: var(--black-color);
  cursor: pointer;
  animation: scroll 3s infinite 0s ease-in-out;
  filter: invert(1);
  height: 80px;
  width: 80%;
}
.scroll-down img {
  height: 80px;
  /* transform: translateX(-50%); */
}
@keyframes scroll {
  0% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-24px);
  }
  100% {
    transform: translateY(0);
  }
}
.benefits,
.benefits-designers {
  display: flex;
  justify-content: space-between;
  align-items: stretch;
  height: fit-content;
  background-color: var(--black-color);
  color: #fff;
  flex-direction: column;
  padding: 2rem 10%;
  min-height: 90vh;
  position: relative;
}
.benefits h2,
.benefits-designers h2 {
  font-size: 4.5rem;
  /* font-weight: 700; */
  margin: 2rem 0;
  background: linear-gradient(45deg, #3281e2, #a31cea, var(--primary-color))
    fixed;
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
.benefits h3,
.benefits-designers h3 {
  font-size: 2.2rem;
  /* font-weight: 700; */
  margin: 2rem 0;
  max-width: 750px;
}
.benefits-designers span {
  background: linear-gradient(0deg, #3281e2, #a31cea, var(--primary-color))
    fixed;
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
.benefits .mini-app {
  display: flex;
  justify-content: center;
  align-items: center;
}

.benefits .mini-app img {
  width: 60%;
  object-fit: contain;
  max-width: 800px;
  border-radius: 16px;
  transition: box-shadow 0.15s ease-in-out; /* Smooth transition for the glow effect */
  box-shadow: 0 0 60px var(--primary-color); /* Glowing effect on hover */
}
.benefits-designers .card-list {
  display: flex;
  justify-content: space-between;
  align-items: start stretch;
  flex-wrap: wrap;
  gap: 4rem;
  padding: 0 5%;
  margin: 2rem 0;
}
.benefits-designers {
  justify-content: center;
}
.benefits-designers .card {
  background-color: #fafafa;
  border-radius: 1rem;
  padding: 2rem;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  transition: all 0.2s ease-in-out;
  width: clamp(300px, 25%, 400px);
  margin: 2rem auto;
  aspect-ratio: 4/5;
  cursor: pointer;
}
.benefits-designers .card:hover {
  scale: 1.05;
}
.benefits-designers .card h3 {
  /* font-size: 1.5rem; */
  margin: 0 0 0.5rem 0;
  font-weight: 700;
  width: 60%;
  background: linear-gradient(45deg, #3281e2, #a31cea, var(--primary-color))
    fixed;
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  margin: 0 40% 0.5rem 0;
}
.benefits-designers .card img {
  width: 60%;
  object-fit: contain;
  margin: 1rem 0;
  transition: filter 0.3s ease-in-out;
}

@media screen and (max-width: 1600px) {
}
@media screen and (max-width: 768px) {
  .main-page-header {
    padding: 1rem;
    flex-wrap: wrap;
    height: fit-content;
    align-items: center;
    justify-content: space-between;
  }
  .main-page-header img {
    height: 40px;
    /* margin: 0 auto ; */
  }
  .main-page-header nav,
  .main-page-header nav ul {
    padding: 0;
    justify-content: space-between;
  }
  .main-page-header nav ul li:first-child {
    display: none;
  }
  .main .glass {
    width: 60%;
    max-width: 350px;
    padding: 2rem;
  }
  .main .glass h1 {
    font-size: 2.2rem;
    line-height: 3rem;
    word-spacing: normal;
  }
  .main .glass h2 {
    font-size: 1.5rem;
  }
  .main {
    height: fit-content;
    min-height: 90vh;
    background: url("./Images/963006959fadd3a9fdcfa557e5469cf9.jpg") fixed
      no-repeat center top/cover;
  }
  .main a {
    margin-top: 1rem;
  }
  .benefits h2,
  .benefits-designers h2 {
    font-size: 2.4rem;
  }
  .benefits h3,
  .benefits-designers h3 {
    font-size: 1.6rem;
  }
  .benefits {
    justify-content: space-around;
    min-height: 75vh;
  }
  .benefits .mini-app img {
    width: 110%;
  }
  .card {
    width: 80% !important;
    margin: 0 auto;
  }
  .benefits-designers {
    padding-top: 2rem;
    padding-bottom: 3rem;
  }
  .benefits-designers a {
    position: relative !important;
    text-align: start;
    margin: 2rem 0 0 35% !important;
  }
  .card h3 {
    width: 100% !important;
    margin: 0 !important;
  }
}
.card {
  width: clamp(150px, 20%, 300px) !important;
  min-width: none;
  margin: 0 !important;
  aspect-ratio: 3/4 !important;
}
.card-list {
  gap: 3rem !important;
  justify-content: center !important;
}
.benefits-designers a {
  position: absolute;
  bottom: 1rem;
  left: 5vw;
  margin-right: 10%;
}
footer {
  background: linear-gradient(45deg, #3281e2, #a31cea, var(--primary-color));
  color: var(--black-color);
  text-align: center;
  padding: 0.8rem 0;
  font-size: 1.2rem;
  margin: 0 0 0;
  position: relative;
}
